<template>
    <button @click='atClicked' v-bind:class="customClass" class="rounded">
        <template v-if="isLoading">
            Loading...<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
            
        </template>
        <template v-else>
            {{buttonText}}
        </template>
    </button>
</template>

<script>
export default {
    name: 'CustomButtom',
    inheritAttrs: true,
    props: {
        buttonText : {
            Type: String,
            default () {
                return 'Hello'
            }
        },
        isLoading : {
            Type: Boolean,
            default () {
                return false
            }
        },
        customClass : {
            Type: String,
            default () {
                return 'btn'
            }
        }
    },
    methods: {
        atClicked () {
            this.$emit('atClicked')
        }
    }

}
</script>

<style scoped>
.btn-danger {
    background: #a02;
}
.btn {
    font-weight: 200
}
button {
    font-size: 10px !important;
}
</style>